import * as React from 'react';
import styled from "styled-components";

export default class MailToBanner extends React.PureComponent<MailToBannerProps> {

    render() {
        const {address, subject, text} = this.props;
        return (
            <MailToBannerRoot>
                <a href={'mailto:' + address + '?subject=' + subject}
                   target="_blank">
                    {text}
                </a>
            </MailToBannerRoot>
        );
    }
}

interface MailToBannerProps {
    address: string;
    subject: string;
    text: string;
}

const MailToBannerRoot = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 100%;
  text-align: center;
  box-shadow: 5px 7px 7px #888888;
  margin-top: 20px;
  
  background: -moz-radial-gradient(center, ellipse cover, #a7182c 1%, #aa0306 44%, #890002 100%); /* FF3.6-15 */
  background: -webkit-radial-gradient(center, ellipse cover, #a7182c 1%,#aa0306 44%,#890002 100%); /* Chrome10-25,Safari5.1-6 */
  background: radial-gradient(ellipse at center, #a7182c 1%,#aa0306 44%,#890002 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  
  a {
    color: white;
    font-weight: bold;
    text-shadow: 2px 2px black;
  }
`;